import { Component } from '@angular/core';
import { DomService } from '../service/DomService';

@Component({
    selector: 'app-table',
    templateUrl: './ui-table.component.html'
})
export class UITableComponent {
    /** è¡æ° */
    row: string = '2';
    /** åæ° */
    col: string = '2';
    handler: any;
    parent: any;
    constructor(
        private domService: DomService
    ) {
    }

    emitTableHTML() {
        const reg = /[1-9]{1,2}/;
        if (!reg.test(this.row)) {
            this.domService.tost({
                text: "è¡æ°ä¸åè¦æ±~"
            });
            return;
        }
        if (!reg.test(this.col)) {
            this.domService.tost({
                text: "åæ°ä¸åè¦æ±~"
            });
            return;
        }
        let html = '<div><table style="width:100%"><tbody>';
        const r = Number(this.row);
        const c = Number(this.col);
        for (let i = 0; i < r; i++) {
            let tr = "<tr>";
            for (let j = 0; j < c; j++) {
                tr += "<td>" /* + input */ + "</td>";
            }
            html += tr + "</tr>";
        }
        html += "</tbody></table></div><p><br/></p>";
        if (this.handler.recieveTableHTML(html)) {
            this.parent.close();
        }
    }
}
