import { Component, ViewChild, ElementRef } from '@angular/core';
import { Radio} from '../_form/radio-group/radio-group';
import { DomService } from '../service/DomService';
import { TipComponent } from '../_alert/tip/tip';
import { AppZeditorComponent } from '../ng-zeditor.component';

@Component({
    selector: 'app-annex',
    templateUrl: './ui-annex.component.html'
})
export class UIAnnexComponent {
    url: string = "https://";
    width: string = "100%";
    height: string = "200px";
    /** è·åç±»åå¯¹åºçåç§° */
    typeName: string = 'å¾ç';
    /** éæ¸²æinput file */
    rebuild: boolean = true;
    radioGroup: Radio[] = [{ value: "image", text: "å¾ç" }, { value: "audio", text: "é³é¢" }, { value: "video", text: "è§é¢" }];
    /** å¾çç±»å */
    static IMAGEARR = ["image/gif", "image/jpeg", "image/jpg", "image/png", "image/svg"];
    static AUDIOARR = ["audio/mp3", "audio/ogg", "audio/wav"];
    static VIDEOARR = ["video/mp4", "video/ogg", "video/webm"];
    @ViewChild('file', { static: false, read: ElementRef }) file: ElementRef;
    // tslint:disable-next-line: variable-name
    _type: 'image' | 'audio' | 'video' = "image";
    handler: any;
    parent: any;
    set type(v: 'image' | 'audio' | 'video') {
        this._type = v;
        this.typeName = {image: 'å¾ç', audio: 'é³é¢', video: 'è§é¢'}[v];
    }
    get type() {
        return this._type;
    }

    constructor(
        private domService: DomService
    ) { }

    /**
     * ç¹å»æ¬å°ä¸ä¼ 
     */
    selectFile() {
        // éè¦åè®¾ç½®å®½åº¦åé«åº¦
        const num = /^[1-9]\d{1,3}(px|rem|em|vw|vh|%)?$/i;
        if (!num.test(this.width + "") || !num.test(this.height + "")) {
            this.domService.tost({
                text: `ä¸ä¼ ${this.typeName}åè¯·å¡«ååéçé«åº¦åå®½åº¦~`
            });
            return;
        }
        const file = this.file.nativeElement;
        const arr = {
            image: UIAnnexComponent.IMAGEARR,
            audio: UIAnnexComponent.AUDIOARR,
            video: UIAnnexComponent.VIDEOARR,
        }[this.type];
        file.accept = arr.join(",");
        file.click();
        file.onchange = this.fileChange;
    }

    /**
     * éæ©æä»¶
     */
    fileChange = () => {
        const files = this.file.nativeElement.files;
        const file = files[0];
        if (!files.length) { return; }
        this.rebuild = false;
        setTimeout(() => { this.rebuild = true; });
        // ç¼è¾å¨å®ä¾
        const handler: AppZeditorComponent = this.handler;
        // è·åç¼è¾å¨banneréç½®åæ°
        const option = handler.options$[this.type] || {};
        // æ ç­¾
        const tag = { image: 'IMG', audio: 'AUDIO', video: 'VIDEO' }[this.type];
        // å¤æ­æä»¶æ¯å¦è¶è¿æ°é
        if (handler.pannel.getElementsByTagName(tag).length >= (option.count || 1)) {
            this.domService.tost({
                text: `${this.typeName}å·²è¶åºæå¤§æ°é`
            });
            return;
        }
        // å¤æ­æä»¶æ¯å¦éè½¬æbase64
        const base64size = option.base64 || 0;
        if (base64size && file.size <= base64size) {
            // è½¬æbase64
            const fr = new FileReader();
            fr.readAsDataURL(file);
            fr.onload = (event: any) => {
                if (handler.recieveLocalFileHTML(this.getFileHTML(event.target.result))) {
                    this.parent.close();
                }
            };
        } else {
            // äº¤ç»å¤é¨è¿è¡å¤ç
            const tip: TipComponent = this.domService.tost({
                text: "ä¸ä¼ ä¸­~",
                duration: -1
            });
            // tslint:disable-next-line: no-unused-expression
            handler.emitUploadFile && handler.emitUploadFile(this.type, file, this.getFileHTML, (isSuccess: boolean, t?: number) => {
                if (isSuccess) { // ä¸ä¼ æå
                    tip.close();
                    this.parent.close();
                    return;
                }
                if (!t) { // ä¸ä¼ å¤±è´¥
                    tip.close();
                    this.parent.close();
                    tip.open({ text: 'ä¸ä¼ å¤±è´¥~' });
                    return;
                }
                // ä¸ä¼ è¶æ¶
                const timer = setTimeout(() => {
                    clearTimeout(timer);
                    tip.close();
                    this.parent.close();
                    tip.open({ text: 'ä¸ä¼ è¶æ¶~' });
                }, t);
            });
        }
    }
    /**
     * æå¥å¤é¾
     */
    insertOutLink() {
        const hasperc = /^[1-9]\d{1,3}(px|rem|em|vw|vh|%)?$/i;
        if (!hasperc.test(this.width)) {
            this.domService.tost({
                text: "è¯·å¡«ååéçå®½åº¦~"
            });
            return;
        }
        if (!hasperc.test(this.height)) {
            this.domService.tost({
                text: 'è¯·å¡«ååéçé«åº¦~'
            });
            return;
        }
        if (!/^(\/\/|https?:)\/\/.+/.test(this.url)) {
            this.domService.tost({
                text: "é¾æ¥å°åä¸è§è"
            });
            return;
        }
        const html = this.getFileHTML(this.url);
        if (this.handler.recieveFileLinkHTML(html)) {
            this.parent.close();
            this.parent = null;
        }
    }

    /**
     * ä¼ å¥srcå¹¶æ ¹æ®ç±»åè·åæä»¶html
     * @param  src
     */
    getFileHTML = (src: string) => {
        let html = '';
        switch (this.type) {
            case "image":
                html = this.getImageHTML(src);
                break;
            case "audio":
                html = this.getAudioHTML(src);
                break;
            case "video":
                html = this.getVideoHTML(src);
                break;
        }
        return html;
    }

    /**
     * è·åæå¥å¾ççHTML
     * @param src urlæbase64
     */
    getImageHTML(src: string) {
        return (
            '<p style="height:' +
            this.height +
            ';">' +
            '<img src="' +
            src +
            '" style="height:' +
            this.height +
            ';width:' +
            this.width +
            ';object-fit:cover;" />' +
            "</p><br/>"
        );
    }
    /**
     * è·åæå¥é³é¢çHTML
     * @param src url
     */
    getAudioHTML(src: string) {
        const arr = UIAnnexComponent.AUDIOARR;
        // tslint:disable-next-line: max-line-length
        let html = '<p style="text-align:center;height:' + this.height + ';"><audio controls style="display:inline-block;height:' + '100%' + ';width:' + this.width + ';">';
        for (let i = 0, len = arr.length; i < len; i++) {
            html += '<source src="' + src + '" type="' + arr[i] + '">';
        }
        html += 'æ¨çæµè§å¨ä¸æ¯æAudioæ ç­¾ã';
        html += '</audio>&#8205;&zwj;</p><br/>';
        return html;
    }

    /**
     * è·åæå¥è§é¢çHTML
     * @param src
     */
    getVideoHTML(src: string) {
        const arr = UIAnnexComponent.VIDEOARR;
        // tslint:disable-next-line: max-line-length
        let html = '<p style="text-align:center;height:' + this.height + ';"><video controls style="display:inline-block;height:' + '100%' + ';width:' + this.width + ';">';
        for (let i = 0, len = arr.length; i < len; i++) {
            html += '<source src="' + src + '" type="' + arr[i] + '">';
        }
        html += 'æ¨çæµè§å¨ä¸æ¯æVideoæ ç­¾ã';
        html += '</video>&#8205;&zwj;</p><br/>';
        return html;
    }

}
