    /**
     * æ§è¡å°è£çç¼è¾å½ä»¤
     * @param k å½ä»¤åç§°
     * @param ui æå¼uiå¼¹çª
     * @param v è®¾ç½®å½ä»¤å¼
     * @returns true-è®¾ç½®æåï¼false-è®¾ç½®å¤±è´¥
     */
    cmd(k: string, ui: boolean, v?: any) {
        if (!this.isSupport(k)) {
            this.toast('ç³»ç»ä¸æ¯æè¯¥å½ä»¤~');
            return false;
        }
        const whiteList = 'insertHTML,paste,cut,copy,removeFormat,delete,selectAll,redo,undo,insertBrOnReturn';
        if (whiteList.indexOf(k) < 0 && this.isRangeInCode()) {
            this.toast('ä»£ç åºåæ æ³æ§è¡è¯¥å½ä»¤~');
            return false;
        }
        const r = document.execCommand(k, ui, v || '');
        return r;
    }

